.listContainer {
  background-color: #000;
  color: #fff;
  text-align: center;
  min-height: 100vh;
  padding-bottom: 20px;
}
.mainTitle {
}
.postList {
  list-style: none;
  padding: 0;
}

.searchResults {
  list-style: none;
  padding: 0;
  color: #fff;
}
.cardContainer {
  display: grid;
  gap: 20px;
  justify-content: center;
  max-width: 1200px;
  margin: 0 auto;
}

.cardRow {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}
